@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*{
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
  display: flex;
}
.sidebar{
    display: flex;
    flex-direction: column;
    flex: 0.25 1;
    max-height: 100vh;
    background-color: #2f3135;
}


.sidebar__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    background-color: #2f3135;
    color: white;
    border-bottom: 3px solid #26282c;
}

.sidebar__channels{
    flex: 1 1;    
}

.sidebar__addChannel {
    cursor: pointer;
}

.sidebar__addChannel:hover {
    color: white;
}

.sidebar__channelsHeader{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #2f3135;
    color: gray;
}

.sidebar__header{
    display: flex;
    align-items: center;
}

.sidebar__voice,
.sidebar__profile {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    color: gray;
    background-color: #2f3135;
    border-top: 1px solid gray;
}

.sidebar__channelList {
    flex: 1 1;
    /* overflow-y: scroll; */
     scrollbar-width: none;
}

.sidebar__channelsList:-webkit-scrollbar{
    display: none;
    scroll-behavior: smooth;
}

.sidebar__channelList {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.sidebar__voiceInfo{
    flex:1 1;
    padding: 10px;
}

.sidebar__voiceIcon {
    color: #4fb185;
}

.sidebar__voiceInfo > h3 {
    color: #4fb185;
}

.sidebar__voiceInfo > p {
    font-size: smaller;
}

.sidebar__voiceIcons > .MuiSvgIcon-root {
    padding: 10px ;
}

.sidebar__profileInfo{
    flex: 1 1;
    padding: 10px;
}

.sidebar__profileInfo > h3 {
    color: white;
}

.sidebar__profileIcon > .MuiSvgIcon-root {
    padding: 10px;
} 



 .sidebarChannel:hover > h4 {
     color: white;
     background-color: #40464b;
 }

 .sidebarChannel > h4 {
     display: flex;
     padding-left: 15px;
     align-items: center;
     background-color: #2f3335;
     color: gray;
     cursor: pointer;
 }

 .sidebarChannel__hash {
     font-size: 30px;
     padding: 8px;
 }
.chat{
    display: flex;
    flex-direction: column;
    flex: 1 1;
    background-color: #353a3f;
    height: 100vh;
}

.chat__messages {
    flex: 1 1;
    overflow-y: scroll;
}

.chat__messages::-webkit-scrollbar{
    display: none;
}

.chat__messages {
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.chat__input {
    color: lightgray;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    border-radius: 5px;
    margin: 20px;
    border-top: 1px solid gray;
    background-color: #474b53;
}

.chat__input > form > input {
    padding: 15px;
    background-color: transparent;
    border: none;
    outline-width: 0;
    color: white;
    font-size: large;
    width: 100%;
}

.chat__input > form {
    flex: 1 1;
}

.chat__inputButton {
    display: none;
}

.chat__inputIcons > .MuiSvgIcon-root {
    padding: 5px;
}
.chatHeader{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: gray;
    padding: 10px;
}

.chatHeader__hash {
    color:gray;
    font-size: 30px;
    padding: 10px
}

.chatHeader__left > h3 {
    display: flex;
    align-items: center;
    color: white;
}

.chatHeader__right{ 
    display: flex;
    align-items: center;
    flex: 0.5 1;
    justify-content: space-between;
}

.chatHeader__right > .MuiSvgIcon-root {
    padding: 5px;
    cursor: pointer;
}

.chatHeader__right > .MuiSvgIcon-root:hover{
    color: white;
}

.chatHeader__search{
    display: flex;
    align-items: center;
    color: gray;
    background-color:#2f3135;
    border-radius: 3px;
    padding: 3px;
}

.chatHeader__search > input{
    background-color: transparent;
    outline-width: 0;
    color: white;
    border: none;
}  
.message {
    display: flex;
    align-items: center;
    padding: 20px;
    color: white;
}

.message__info {
    margin-left: 20px;
}

.message__timestamp {
    color: gray;
    margin-left: 20px;
    font-size: x-small;
}
.login{
    display: grid;
    place-items: center;
    height: 100vh;
    width: 100%;
}

.login > button {
    width: 200px;
    background-color: #738adb;
    color: #eff2f5;
    font-weight: 800;
}

.login > button:hover {
    background-color: black;
    color: #738adb;
}

.login > div > img {
    object-fit: contain;
    height: 150px;
}
